<template>
  <div class="mapContainer" id="mapContainer" @click="flyToTarget"></div>
  <br>
  <div id="operate">
    <button id="downdata" @click="downFeature">下载图形</button>
    <button id="printmap" @click="printMap">下载地图</button>
  </div>

</template>

<script>
import "leaflet/dist/leaflet.css";
import { toRaw } from "vue";
import L from "leaflet";
import cc from "leaflet.vectorgrid";
import axios from 'axios'
import "heatmap.js";
import HeatmapOverlay from "heatmap.js/plugins/leaflet-heatmap";
import "leaflet.timeline.control";
import "leaflet-easyprint";
export default {
  name: "Leaflet",
  data() {
    return {
      map: null,
      vecLayer: null,
      vecLayer_poly: null,
      layerControl: null,
      overlays: {},
      baseLayers: null,
      heatmapLayer: null,
      mapprinter: null
    };
  },
  mounted() {
    const map = L.map("mapContainer").setView([29.55, 106.55], 12)

    var tileLight = L.tileLayer(
      "https://{s}.basemaps.cartocdn.com/light_all/{z}/{x}/{y}{r}.png"
    ).addTo(map);

    var stamenLayer = L.tileLayer("https://server.arcgisonline.com/arcgis/rest/services/World_Imagery/MapServer/tile/{z}/{y}/{x}", {
      attribution: "stamen"
    });

    this.baseLayers = {
      '明亮地图': tileLight,
      '暗色地图': stamenLayer
    };

    this.map = map;

    this.addPbfLayer_polygon();
    this.addPbfLayer_point()
    this.layerControl = L.control.layers(null, this.overlays, { collapsed: false }).addTo(this.map);
    //this.layerControl = L.control.layers(this.baseLayers, this.overlays, { collapsed: false }).addTo(this.map);
    this.addjsonLayer()
    return

    // 时间控件
    //var dateNow = (new Date()).format("YYYY-MM-dd");
    const timelineControl = L.control.timeline({
      autoplay: false,
      position: "bottomleft",
      onNextStep: (cur) => this.addjsonLayer(cur),
      interval: 1000,
      button: {
        pausedText: "开始",
        playingText: "停止"
        //render: () => document.createElement("strong")
      },
      timeline: {
        dateFormat: "yyyy-MM-dd",
        // renderSlot: () => document.createElement("h4"),
        // renderActiveSlot: () => document.createElement("h5"),
        range: [new Date("2022-11-18"), new Date()],
        step: {
          day: 1
        }
      }
    });
    map.addControl(timelineControl);

    // 打印控件
    this.mapprinter = L.easyPrint({
      sizeModes: ['Current', 'A4Landscape', 'A4Portrait'],
      filename: "测试地图",
      exportOnly: true,
      hideControlContainer: true
    }).addTo(map);
  },
  methods: {

    addPbfLayer_point() {
      //添加矢量瓦片
      //           https://yqdt.digitalcq.com:8000/yqdtApiV2/services/vtserver/yq_gkfw/tile/10/813/423.pbf
      var url = "https://yqdt.digitalcq.com:8000/yqdtApiV2/services/vtserver/yq_gkfw/tile/{z}/{x}/{y}.pbf";
      var vectorTileOptions = {
        layerURL: url,
        rendererFactory: L.canvas.tile,
        tms: false,
        vectorTileLayerStyles: {
          'yq_gkfw_pt': function (properties, zoom) {
            //this.map.getZoom()
            if (properties.dj == "高风险区") {
              if (zoom <= 13) {
                return {
                  radius: 1,
                  fillColor: "#ef2d30",
                  color: "#ef5a30",
                  weight: 2,
                  opacity: 0.8,
                  fillOpacity: 0.4
                }
              }
              else {
                return {
                  radius: 5,
                  fillColor: "#ef2d30",
                  color: "#ef5a30",
                  weight: 2,
                  opacity: 0.8,
                  fillOpacity: 0.4
                }
              }
            }
            else {
              if (zoom <= 13) {
                return {
                  radius: 1,
                  fillColor: "#a2ff00",
                  color: "#0fff0f",
                  weight: 2,
                  opacity: 0.8,
                  fillOpacity: 0.4
                }
              }
              else {
                return {
                  radius: 5,
                  fillColor: "#a2ff00",
                  color: "#0fff0f",
                  weight: 2,
                  opacity: 0.8,
                  fillOpacity: 0.4
                }
              }
            }


          },
          'yq_gkfw_py': function (properties, zoom) {
            return {
              weight: 0,
              fillColor: '#ff5652',
              fillOpacity: 0.0,
              fill: true
            }
          },
        },
        interactive: true,	//开启VectorGrid触发mouse/pointer事件
        getFeatureId: function (f) {
          return f.properties.name;
        }
      };

      var vectorTile = L.vectorGrid.protobuf(url, vectorTileOptions).addTo(this.map);
      vectorTile.on('click', function (e) {
        var properties = e.layer.properties;
        debugger
        L.popup().setContent("风险等级：" + properties.dj + "<br> 位&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp置：" + properties.name).setLatLng(e.latlng).openOn(this._map);
      });
      this.vecLayer = vectorTile
      this.overlays.风险点 = vectorTile
    },
    addPbfLayer_polygon() {
      //添加矢量瓦片
      //           https://yqdt.digitalcq.com:8000/yqdtApiV2/services/vtserver/yq_gkfw/tile/10/813/423.pbf
      var url = "https://yqdt.digitalcq.com:8000/yqdtApiV2/services/vtserver/yq_gkfw/tile/{z}/{x}/{y}.pbf";
      var vectorTileOptions = {
        layerURL: url,
        rendererFactory: L.canvas.tile,
        tms: false,
        vectorTileLayerStyles: {
          'yq_gkfw_pt': function (properties, zoom) {
            return {
              radius: 1,
              fillColor: "#ef2d30",
              color: "#ef5a30",
              weight: 2,
              opacity: 0.0,
              fillOpacity: 0.0
            }
          },
          'yq_gkfw_py': function (properties, zoom) {
            if (properties.dj == "高风险区") {
              return {
                weight: 0,
                fillColor: '#ff5652',
                fillOpacity: 0.5,
                fill: true
              }
            } else {
              return {
                weight: 0,
                fillColor: '#a2ff00',
                fillOpacity: 0.4,
                fill: true
              }
            }

          },
        },
        interactive: true,	//开启VectorGrid触发mouse/pointer事件
        getFeatureId: function (f) {
          return f.properties.name;
        }
      };

      var vectorTile = L.vectorGrid.protobuf(url, vectorTileOptions).addTo(this.map);
      vectorTile.on('click', function (e) {
        var properties = e.layer.properties;
        debugger
        L.popup().setContent("风险等级：" + properties.dj + "<br> 位&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp置：" + properties.name).setLatLng(e.latlng).openOn(this._map);
      });
      this.vecLayer_poly = vectorTile
      this.overlays.风险范围 = vectorTile
    },

    styleFeature(feature, latlng) {
      var Style = {
        radius: 4,
        fillColor: "#FF4500",
        color: "#FF7256",
        weight: 2,
        opacity: 0.8,
        fillOpacity: 0.4
      };

      if (this.map.getZoom() <= 12) {
        Style.opacity = 0;
        Style.fillOpacity = 0
      }
      else {
        Style.opacity = 0.8;
        Style.fillOpacity = 0.4
      }
      return L.circleMarker(latlng, Style);
    },
    addjsonLayer(date) {
      var Style = {
        radius: 4,
        fillColor: "#FF4500",
        color: "#FF7256",
        weight: 2,
        opacity: 0.8,
        fillOpacity: 0.4
      };
      var heatopt = {
        radius: 0.002,
        maxOpacity: 0.5,
        minOpacity: 0.1,
        scaleRadius: true,
        blur: 1,
        useLocalExtrema: true,
        latField: "lat", //维度
        lngField: "lng", //经度
        valueField: "count", //热力点的值
        gradient: {
          "0.99": "rgba(255,0,0,1)",
          "0.9": "rgba(255,255,0,1)",
          "0.8": "rgba(0,255,0,1)",
          "0.5": "rgba(0,255,255,1)",
          "0": "rgba(0,0,255,1)"
        }
      };
      var that = this;

      var datasrc = "/datas/point-2022-11-18.json"
      if (date) {
        datasrc = "/datas/point-" + date.getFullYear() + "-" + (date.getMonth() + 1) + "-" + date.getDate() + ".json"
      }
      axios.get(datasrc).then((res) => {
        console.log('res.data = ', res.data)

        //点状图
        // var layerGeo = L.geoJSON(res.data, {
        //   pointToLayer: function (feature, latlng) {
        //     //return L.circleMarker(latlng, Style);
        //     return that.styleFeature(feature, latlng)
        //   },
        //   // style: this.styleFeature(feature, latlng),
        //   minZoom: 13,
        //   maxZoom: 18

        // }).addTo(this.map);
        // this.overlays.风险分布图 = layerGeo

        // 热力图
        var heatPoints = [];
        for (var i = 0; i < res.data.features.length; i++) {
          heatPoints[i] = { lat: res.data.features[i].properties.lat, lng: res.data.features[i].properties.lng, count: res.data.features[i].properties.dj == "高风险区" ? 1 : 0.5 };
        }
        var Data = {
          max: 1,
          data: heatPoints
        };
        if (this.heatmapLayer == null) {
          this.heatmapLayer = new HeatmapOverlay(heatopt);
          this.layerControl.addOverlay(this.heatmapLayer, "热力图");
        }
        this.heatmapLayer.addTo(this.map);
        this.heatmapLayer.setData(Data);
        // this.overlays.热力图 = this.heatmapLayer

      })

    },
    saveJSON(data, filename) {
      if (!data) {
        alert('data is null');
        return;
      }
      if (!filename) {
        filename = 'json.json'
      }
      if (typeof data === 'object') {
        data = JSON.stringify(data, undefined, 4)
      }
      var blob = new Blob([data], { type: 'text/json' });
      var e = document.createEvent('MouseEvents');
      var a = document.createElement('a');
      a.download = filename;
      a.href = window.URL.createObjectURL(blob);
      a.dataset.downloadurl = ['text/json', a.download, a.href].join(':');
      e.initMouseEvent('click', true, false, window, 0, 0, 0, 0, 0, false, false, false, false, 0, null);
      a.dispatchEvent(e);
    },

    downFeature() {
      var featureGeoJson = {
        "type": "FeatureCollection",
        "features": []
      }
      //this.map = map
      var ff = this.vecLayer
      var tiles = toRaw(this.vecLayer._vectorTiles)
      console.log(tiles)
      for (var key in tiles) {
        var tile = tiles[key]
        if (tile._features) {
          var features = tile._features
          for (var fk in features) {
            var feature = features[fk]
            //if(feature.layerName == "yq_gkfw_pt")
            {
              feature = feature.feature
              var point = {
                "type": "Feature",
                "properties": feature.properties,
                "geometry": {
                  "coordinates": [
                    feature.properties.lng,
                    feature.properties.lat
                  ],
                  "type": "Point"
                }
              }
              featureGeoJson.features.push(point)
            }
          }
        }
      }
      console.log(featureGeoJson)
      this.saveJSON(featureGeoJson, "point_yq.json")
    },
    printMap() {
      var date = new Date()
      var name = date.getFullYear() + "-" + (date.getMonth() + 1) + "-" + date.getDate()

      this.mapprinter.printMap('CurrentSize', name + '疫情地图')
    }

  }
};
</script>

<style>
html,
body {}

.mapContainer {
  position: relative;
  height: 100%;
  width: 100%;
  z-index: 100;
  border: 1px solid black;
}

#operate {
  z-index: 11111;
  position: absolute;
  bottom: 10px;
  right: 10px;
}
</style>